function RoomRow(props) {
  const rooms = props.rooms
  const roomLink = `/rooms/${rooms.id}`
  const updateroom = `room/update/${rooms.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={rooms.id.toString()}>
      <th scope="row"><Link to={roomLink}>{rooms.id}</Link></th>
      <td ><Link to={roomLink}>{rooms.room_no}</Link></td>
      {/* <td>{ rooms.ward_id}</td> */}
      <td >{rooms.status==1?'Enable':'Disable'}</td>
      <td ><Link to={updateroom}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

class Rooms extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }

  componentDidMount() {
    this.getAllRooms(1)
  
  }
  getAllRooms=async(page)=>{
    const responseData= await getAllRooms(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllRooms(currentPage)
    console.log(data);
  };



  render() {

    const roomList = this.state.items; //roomsData.filter((room) => room.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total rooms records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={2}>
          </Col>
          <Col xl={8}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Rooms 
                <Link to={addroom}><button onClick={this.showHospitalInfo} className='btn btn-success  upActionbtn'>+ Add room</button></Link>
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Room No</th>
                      {/* <th scope="col">Ward Name</th> */}
                      <th scope="col">Status</th>
                      <th scope="col">Edit room</th>
                    </tr>
                  </thead>
                  <tbody>
                    {roomList.map((rooms, index) =>
                      <RoomRow key={index} rooms={rooms}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Rooms;
